export default {
  name: 'uni-tab',
  data() {
    return {
      average: 0,
      back: false
    };
  },
  props: {
    value: {
      type: Number, //当前选中下标
      default() {
        return 0;
      }
    },
    tabList: {
      type: Array,
      default() {
        return [];
      }
    },
    bgColor: { //背景颜色
      type: String,
      default() {
        return '#FFFFFF';
      }
    },
    defaultColor: { //默认未选中文字颜色
      type: String,
      default() {
        return '#000000';
      }
    },
    activeColor: { //选中时文字颜色 线条颜色
      type: String,
      default() {
        return '#1e9fff';
      }
    },
    rangeKey: { // 当tabList为对象时 显示指定下标值
      type: String,
      default() {
        return '';
      }
    },
    scroll: { //横向滑动
      type: Boolean,
      default() {
        return false;
      }
    },
  },
  computed: {
    barLeft() {
      return this.value * this.average;
    },
    barRight() {
      let index = this.tabList.length - this.value - 1;
      return index * this.average;
    },
  },
  created() {
    this.average = 100 / this.tabList.length;
  },
  methods: {
    itemClick(index, tab) {
      console.log('我点击了tab')
      if (this.value == index) return false;
      if (this.value > index) {
        this.back = true;
      } else {
        this.back = false;
      }
      // this.value = index;
      this.$emit('update:value', index);
      this.$emit('change', { tab: tab });
    }
  }
};
